body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


article {
  background-color: #fff;
  border-radius: 5px;
  box-shadow: 0 3px 3px rgba(0, 0, 0, .2)
}

article img {
  padding-top: 5px;
  border-radius: 50%;
  transition: 40ms;
}

article p {
  padding-left: 20px;
  text-align: left;
}

article p {
  font-weight: bold;
  font-size: 1.2rem;
}

article div {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
}

.social-media {
  height: 30px;
  border-radius: 30%;
}
*{
    margin: 0;
}

:root {
    --black: rgb(8, 8, 8);
}

header {
    margin: 0;
    padding-left: 10px;
    background-color: rgb(8, 8, 8);
    background-color: var(--black);
    color: white;
    height: 70px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    text-align: center;
    position: relative;
    box-shadow: 0 5px 3px rgba(0, 0, 0, 0.452);
}

header ul{
    width: 25%;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 15px;
    gap: 15px;
    position: relative;
    height: 100%;
}

header ul li {
    list-style: none;
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    transition: .4s ease-out;
    cursor: pointer;
}

header ul li:hover {
    background-color: rgba(252, 250, 250, 0.322);
}

.info {
    margin: 0;
    min-height: 50vh;
    background-image: url('https://www.solofondos.com/wp-content/uploads/2016/07/wallpapers-hd-para-pc-tumblr-1024x576.jpg');
}

.info-container {
    color: white;
    padding: 5px;
}

.cards-header-separator p {
    margin: 10px;
}

footer {
    position: absolute;
    right: 0;
    left: 0;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    color: white;
    height: 50px;
    background-color: rgb(8, 8, 8);
    background-color: var(--black);
    position: absolute;
    width: 100%;
}
.custom-link {
    color: white;
}
.App {
  text-align: center;
  background-color: rgb(219, 219, 219);
  margin: 0;
}

section {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  grid-gap: 15px;
  gap: 15px;
  padding: 20px;
}

